.m-10{margin:10px}.m-20{margin:20px}.mb-30{margin-bottom:30px}.mb-40{margin-bottom:40px}.p-0{padding:0}.p-5{padding:5px}.p-10{padding:10px}.p-15{padding:15px}.br-5{border-radius:5px}.d-flex-wrap{display:flex;flex-wrap:wrap}.d-none{display:none}.mw-50{max-width:50%}.w-100{width:100%}*{box-sizing:border-box}body{max-width:700px;margin:0;display:grid;grid-template-rows:48px auto 48px;position:fixed;left:0;top:0;width:100%;height:100%;grid-template-areas:'header' 'main' 'footer';font-size:112.5%;line-height:1.5;color:#455a64;font-family:'Roboto', sans-serif}header{grid-area:header;box-shadow:0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.12),0 1px 5px 0 rgba(0,0,0,0.2)}main{grid-area:main;overflow:scroll}h1{text-align:center;margin:0;font-size:1.25em}h1,h2,h3,label{font-family:'Roboto Slab', serif}button{display:block;font-size:1em;background-color:#337ab7;color:white;margin:0 auto;border-radius:5px}label{display:block}input,textarea{width:100%;border-radius:5px}textarea{font-size:1.23077em}input{font-size:1em}li{list-style:none;font-size:0.875 em;padding:5px 10px;border-radius:16px;background-color:#aaa;margin-bottom:10px;margin-right:10px}.header__headline{margin-top:8px}.footer{grid-area:footer}.footer__nav{display:grid;grid-template-columns:repeat(4, 1fr);justify-items:center;align-items:center;box-shadow:0 -2px 2px 0 rgba(0,0,0,0.14),0 -3px 1px -2px rgba(0,0,0,0.12),0 -1px 5px 0 rgba(0,0,0,0.2);padding-top:3px}.footer__icon{line-height:1;width:32px}.footer__icon--active{filter:invert(1);background-color:white;border-radius:50%;width:40px;padding:2px}.card{position:relative;border:1px solid black;border-radius:5px;box-shadow:3px 3px 7px 1px grey}.card__bookmark{position:absolute;right:15px;top:-8px;height:48px;width:48px;background-color:transparent}.card__bookmark--active{color:black;fill:crimson}.card__bookmark--inactive{color:grey;fill:transparent}.form{justify-content:stretch}.form__input-wrapper{width:100%}.profile{align-items:center;gap:15px}.profile__picture{border-radius:50%}.profile__user-name{font-size:1.2em}
